.section {
    padding: 80px 20px;
}

.new-watches-text {
    color: rgba(20, 20, 20, 0.40);
    font-size: 12px;
    font-weight: 300;
    line-height: 14px;
    letter-spacing: 0.72px;
    margin-bottom: 8px;
}

.title {
    color: var(--black, #141414);
    font-size: 32px;
    font-weight: 600;
    line-height: 40px;
    letter-spacing: -0.64px;
    margin-bottom: 40px;
}

.new-watches-list {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    gap: 20px;
    overflow: hidden;
}

.new-watches-item {
    width: 335px;
    height: 260px;
}

.green {
    background: url(../images/nwatch/green1x.jpg), lightgray 50% / contain no-repeat;
    background-size: 100%;
}

.bronze {
    background: url(../images/nwatch/bronze1x.jpg), lightgray 50% / cover no-repeat;
    background-size: 100%;
}

@media screen and (min-width:768px) {

    .section {
        padding: 128px 32px;
    }

    .title {
        color: var(--dark-green, #1E2827);
        font-size: 40px;
        font-style: normal;
        font-weight: 600;
        line-height: 44px;
        /* 110% */
        letter-spacing: -0.8px;
    }

    .new-watches-list {
        flex-direction: row;
        flex-wrap: nowrap;
    }

    .new-watches-item {
        width: 342px;
    }
}

@media screen and (min-width:1280px) {
    .section {
        padding: 80px 132px;
    }

    .title {
        font-size: 40px;
        line-height: 44px;
        letter-spacing: -0.8px;
        width: 448px;
        margin-bottom: 80px;
    }

    .new-watches-list {
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: nowrap;
        gap: 24px;
    }

    .new-watches-item {
        width: 576px;
        height: 422px;
    }

}