  z-index: 2;
  align-items: center;
  gap: 4px;
  display: flex;
  position: relative;
}

.header-logo-text {
  letter-spacing: -.36px;
  color: var(--white);
  font-weight: 400;
  line-height: 1;
}

.header-navigation {
  justify-content: center;
  align-items: center;
  margin-right: auto;
  display: flex;
}

.header-list {
  display: flex;
}

.burger {
  display: none;
}

@media screen and (max-width: 767px) {
  .header {
    padding: 29px 0;
  }

  .header-mobile {
    max-width: 767px;
    padding: 0 20px;
  }

  .header-navigation {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background: var(--dark-green);
    transition: 600ms var(--cubic);
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    transform: translate(0%, -100%);
  }

  .header-navigation.is-open {
    transform: translate(0%);
  }

  .header-list {
    color: var(--white);
    flex-direction: column;
    justify-content: center;
    gap: 14px;
  }

  .header-link {
    text-align: center;
    color: var(--white);
    transition: var(--cubic);
    display: block;
  }

  .underline {
    width: auto;
    display: inline-block;
    position: relative;
  }

  .underline:before {
    content: "";
    width: 100%;
    height: 1px;
    background: var(--white);
    opacity: .7;
    width: 0%;
    transition: width 250ms var(--cubic);
    position: absolute;
    top: 95%;
    left: auto;
    right: 0;
  }

  .underline:hover:before, .underline:focus:before {
    width: 100%;
    left: 0;
    right: auto;
  }

  .header-contact-phone {
    display: none;
  }

  .img-logo {
    width: 14px;
    height: 14px;
  }

  .header-logo {
    margin-right: auto;
  }

  .header-logo-text {
    letter-spacing: -.36px;
    color: var(--white);
    justify-content: center;
    align-items: center;
    font-family: epilogue;
    font-size: 12px;
    font-weight: 400;
    line-height: 1;
    display: flex;
  }

  .burger .burger-svg {
    background-color: var(--black);
    display: block;
  }

  .burger .close-svg, .burger.is-open .burger-svg {
    display: none;
  }

  .burger.is-open .close-svg {
    display: block;
  }

  .burger {
    z-index: 20;
    fill: var(--white);
    cursor: pointer;
    transition: var(--cubic);
    background-color: rgba(0, 0, 0, 0);
    border: none;
    align-items: center;
    display: flex;
    position: absolute;
    top: 50%;
    right: 20px;
    transform: translate(0%, -50%);
  }

  .burger:hover {
    background-color: var(--dark-green);
    border-radius: 2px;
  }
}

