.modal {
  width: 100%;
  height: 100%;
  z-index: 10;
  pointer-events: auto;
  opacity: 1;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  transition-property: visibility, opacity;
  transition-duration: .25s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
  transform: none;
  box-shadow: 0 0 8.77546px rgba(0, 0, 0, .01), 0 0 18.1704px rgba(0, 0, 0, .02), 0 0 36.2375px rgba(0, 0, 0, .03), 0 0 71.0296px rgba(0, 0, 0, .03), 0 0 130.6px rgba(0, 0, 0, .04), 0 0 223px rgba(0, 0, 0, .05);
}

.modal.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
